import React, { useState } from 'react'
import { Button, Card, Col, Container, Row, Image, Modal } from 'react-bootstrap'
import Highlight from '../components/Highlight'
import Experience from '../components/Experience'
import Education from '../components/Education'
import Projects from '../components/Projects'
import Resume from '../components/Resume'
import generalInfo from '../data/general'

const HomeScreen = () => {
    const [resumeShow, setResumeShow] = useState(false)
    return (
        <>
            <Highlight id="#top"/>

            <Experience />

            <Education />

            <Projects />

            <Container className="text-center my-5" data-aos="flip-left" data-aos-once="false">
                <Row>
                    <Col>
                        <h1 className="copernicus-bold-font">Hire Me?</h1>
                        {/* <Row classnme="justify-content-center align-items-center text-center"> */}
                            <p>
                                <Button variant="outline-success m-1 font-weight-bold lato" href={`mailto:${generalInfo.email}`} target="_blank" rel="noreferrer"><i className="fas fa-envelope fa-2x"></i>{generalInfo.email}</Button>
                                <Button variant="outline-info m-1 font-weight-bold lato" href={generalInfo.linkedin} target="_blank" rel="noreferrer"><i className="fab fa-linkedin fa-2x"></i>Mohit M.</Button>
                                <Button variant="info m-1" onClick={() => setResumeShow(true)}>My Resume</Button>
                            </p>
                        {/* </Row> */}
                    </Col>
                </Row>
                <Modal
                    size="xl"
                    show={resumeShow}
                    onHide={() => setResumeShow(false)}
                    aria-labelledby="example-modal-sizes-title-lg"
                >
                    <Modal.Header id="myResume" closeButton>
                        <Modal.Title id="example-modal-sizes-title-lg">
                            My Resume
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body className="p-2">
                        <Resume />
                    </Modal.Body>
                    <Modal.Footer className="p-0">
                        <Button className="btn-block" onClick={() => setResumeShow(false)}>Close</Button>
                    </Modal.Footer>
                </Modal>
            </Container>
        </>
    )
}

export default HomeScreen
